<template>
  <div class="contain">
    <div class="main">
      <ul>
        <li v-for="item,index in infoList">
          <div class="floor">#{{index+1}}</div>
          <div class="other">
            <div class="you">@{{item.name}}说：</div>
            <div class="text">{{item.text}}</div>
            <div class="time">{{item.time}}</div>
          </div>
          <div class="mine" v-if="item.reverse">
            <div class="my">回复：</div>
            <div class="text">{{item.reverse.text}}</div>
            <div class="time">{{item.reverse.time}}</div>
          </div>
        </li>

      </ul>
    </div>
    <div class="message_board">
      <div class="row">
        <div class="name">
          <input type="text" placeholder="你的名字" id="name" v-model="name">
          <label for="name" class="label">你的名字</label>
        </div>
        <div class="email">
          <input type="email" placeholder="你的邮箱" id="email" v-model="email">
          <label for="email" class="label">你的邮箱</label>
        </div>
      </div>
      <div class="row">
        <div class="mes">
          <input type="text" placeholder="你想说什么" id="text" v-model="word">
          <label for="text" class="label">你想说什么</label>
        </div>
        <div class="send" @click="send()">
          <button>send</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import "../assets/js/duangduangduang";
import "../assets/js/chat";
import socket from "../assets/js/chat";
export default {
  data() {
    //这里存放数据
    return {
      name: "",
      email: "",
      word: "",
      infoList: [
        {
          name: "张三",
          text: "123456",
          time: "2019-12-12 12:12",
          reverse: { text: "abcd", time: "2019-12-12 12:12" }
        },
        {
          name: "李四",
          text: "abcdef",
          time: "2020-12-12 12:12",
          reverse: { text: "1234", time: "2020-12-12 12:12" }
        },
        {
          name: "张三",
          text: "123456",
          time: "2019-12-12 12:12",
          reverse: { text: "abcd", time: "2019-12-12 12:12" }
        },
        {
          name: "李四",
          text: "abcdef",
          time: "2020-12-12 12:12",
          reverse: { text: "1234", time: "2020-12-12 12:12" }
        },
        {
          name: "张三",
          text: "123456",
          time: "2019-12-12 12:12",
          reverse: { text: "abcd", time: "2019-12-12 12:12" }
        },
        {
          name: "李四",
          text: "abcdef",
          time: "2020-12-12 12:12",
          reverse: { text: "1234", time: "2020-12-12 12:12" }
        }
      ]
    };
  },
  mounted() {
    socket.on("resMsg", msg => {
      this.infoList.push(msg);
    });
  },
  methods: {
    send() {
      let obj = {};
      let time = this.getTime();
      obj.name = this.name;
      obj.word = this.word;
      obj.time = this.getTime();
      //   console.log(obj);
      socket.emit("sendMsg", obj);
      this.name = "";
      this.email = "";
      this.word = "";
    },
    getTime() {
      let data = new Date();
      let year = data.getFullYear();
      let month = data.getMonth() + 1;
      let day = data.getDate();
      let hour = data.getHours();
      let minute = data.getMinutes();
      let second = data.getSeconds();
      hour = hour < 10 ? "0" + hour : hour;
      minute = minute < 10 ? "0" + minute : minute;
      second = second < 10 ? "0" + second : second;
      return `${year}-${month}-${day} ${hour}:${minute}:${second}`;
    }
  }
};
</script>
<style scoped>
@import "../assets/css/contact.css";
</style>


